import React, { useState, useEffect } from 'react';
import ImageGallerySlider from '../ImageGallerySlider';
import NewsServiceManager, { FormattedNewsArticle } from '../../services/newsServiceManager';
import './Footer.css';

interface GalleryImage {
  id: number;
  src: string;
  alt: string;
  title?: string;
  date?: string;
}

const Footer: React.FC = () => {
  const [galleryImages, setGalleryImages] = useState<GalleryImage[]>([]);
  const [popularPosts, setPopularPosts] = useState<FormattedNewsArticle[]>([]);

  // Fallback popular posts
  const fallbackPopularPosts: FormattedNewsArticle[] = [
    {
      id: 101,
      title: 'FOOD REVIEW: KHANOM HOUSE DELIVERS SUBTLE SWEET INDULGENCE',
      date: 'May 24, 2025',
      excerpt: 'A delightful exploration of Thai desserts and their cultural significance...',
      image: 'https://picsum.photos/id/2/100/60',
      category: 'FOOD',
      slug: 'khanom-house-review',
      link: 'https://megaphoneoz.com/khanom-house-review'
    },
    {
      id: 102,
      title: 'SYDNEY THEATRE SCENE: WINTER PRODUCTIONS HEAT UP',
      date: 'May 20, 2025',
      excerpt: 'The best theatrical productions to catch this winter season...',
      image: 'https://picsum.photos/id/15/100/60',
      category: 'ARTS',
      slug: 'sydney-theatre-winter',
      link: 'https://megaphoneoz.com/sydney-theatre-winter'
    },
    {
      id: 103,
      title: 'LOCAL MUSIC FESTIVAL ANNOUNCES LINEUP',
      date: 'May 18, 2025',
      excerpt: 'Exciting new artists join the summer music festival roster...',
      image: 'https://picsum.photos/id/1/100/60',
      category: 'MUSIC',
      slug: 'music-festival-lineup',
      link: 'https://megaphoneoz.com/music-festival-lineup'
    }
  ];

  // Fallback images
  const fallbackImages: GalleryImage[] = [
    {
      id: 1,
      src: "https://picsum.photos/200/150?random=1",
      alt: "Review: Skank Sinatra",
      title: "REVIEW: SKANK",
      date: "June 15, 2025"
    },
    {
      id: 2,
      src: "https://picsum.photos/200/150?random=2",
      alt: "SU Anime Society",
      title: "SU ANIME",
      date: "June 15, 2025"
    },
    {
      id: 3,
      src: "https://picsum.photos/200/150?random=3",
      alt: "Arts Review",
      title: "ARTS REVIEW",
      date: "June 13, 2025"
    },
    {
      id: 4,
      src: "https://picsum.photos/200/150?random=4",
      alt: "Music Concert",
      title: "MUSIC",
      date: "June 1, 2025"
    },
    {
      id: 5,
      src: "https://picsum.photos/200/150?random=5",
      alt: "Theatre Review",
      title: "THEATRE",
      date: "May 28, 2025"
    },
    {
      id: 6,
      src: "https://picsum.photos/200/150?random=6",
      alt: "Film Festival",
      title: "FILM",
      date: "May 25, 2025"
    },
    {
      id: 7,
      src: "https://picsum.photos/200/150?random=7",
      alt: "Cultural Event",
      title: "CULTURE",
      date: "May 20, 2025"
    },
    {
      id: 8,
      src: "https://picsum.photos/200/150?random=8",
      alt: "Exhibition",
      title: "EXHIBITION",
      date: "May 15, 2025"
    },
    {
      id: 9,
      src: "https://picsum.photos/200/150?random=9",
      alt: "Performance",
      title: "PERFORMANCE",
      date: "May 10, 2025"
    },
    {
      id: 10,
      src: "https://picsum.photos/200/150?random=10",
      alt: "Gallery Opening",
      title: "GALLERY",
      date: "May 5, 2025"
    }
  ];

  // Fetch WordPress images and popular posts
  useEffect(() => {
    const fetchData = async () => {
      try {
        // Start with fallback data
        setGalleryImages(fallbackImages);
        setPopularPosts(fallbackPopularPosts);
        
        // Delay footer loading by 2 seconds to avoid concurrent requests
        await new Promise(resolve => setTimeout(resolve, 2000));
        
        const newsService = NewsServiceManager.getInstance();
        const articles = await newsService.getLatestNewsForSlider(10);
        
        if (articles && articles.length > 0) {
          const transformedImages: GalleryImage[] = articles.map(article => ({
            id: article.id,
            src: article.image,
            alt: article.title,
            title: article.title.toUpperCase(),
            date: article.date
          }));
          setGalleryImages(transformedImages);
          
          // Use first 3 articles as popular posts
          setPopularPosts(articles.slice(0, 3));
        }
      } catch (error) {
        console.warn('Error fetching footer data (using fallback):', error instanceof Error ? error.message : error);
        // Fallback data already set above, no need to do anything else
      }
    };

    fetchData();
  }, []);

  return (
    <footer className="footer">
      {/* Image Gallery Slider at the top */}
      <div className="footer-gallery-section">
        <div className="footer-gallery-container">
          <ImageGallerySlider 
            images={galleryImages}
            autoPlay={true}
            autoPlayInterval={3000}
            visibleCount={8}
          />
        </div>
      </div>

      <div className="footer-content">
        {/* French Film Festival Section */}
        <div className="footer-section french-film">
          <h3 className="footer-title">FRENCH FILM FESTIVAL</h3>
          <div className="film-festival-image">
            <img 
              src="https://picsum.photos/id/1/400/250" 
              alt="French Film Festival" 
            />
          </div>
        </div>

        {/* Newsletter Subscription Section */}
        <div className="footer-section newsletter">
          <h3 className="footer-title">YOUR MEGAPHONE OZ SUSCRIBE HERE!</h3>
          <div className="newsletter-form">
            <input 
              type="email" 
              placeholder="@" 
              className="newsletter-input"
            />
            <button className="newsletter-btn">SUBSCRIBE</button>
          </div>
          
        </div>

        {/* Popular Posts Section */}
        <div className="footer-section popular-posts">
          <h3 className="footer-title">POPULAR POSTS</h3>
          {popularPosts.map((post, index) => (
            <div key={post.id} className="popular-post">
              <div className="post-image">
                <img 
                  src={post.image || `https://picsum.photos/id/${index + 1}/100/60`} 
                  alt={post.title}
                  onError={(e) => {
                    const target = e.target as HTMLImageElement;
                    target.src = `https://picsum.photos/id/${index + 1}/100/60`;
                  }}
                />
              </div>
              <div className="post-content">
                <h4 className="post-title">
                  <a href={post.link} target="_blank" rel="noopener noreferrer">
                    {post.title.length > 60 ? `${post.title.substring(0, 60)}...` : post.title}
                  </a>
                </h4>
                <div className="post-meta">
                  <span className="post-date">{post.date}</span>
                  <span className="post-category">{post.category}</span>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Copyright Section */}
      <div className="footer-bottom">
        <p className="copyright">
          <strong>Megaphone Oz</strong> Copyright  2025.
        </p>
      </div>
    </footer>
  );
};

export default Footer;